* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    display: block;
}

head {
    display: none;
}

body {
    background-color: #ddd;
}

.firstContent {
    background-color: yellow;
}

.navbar {
    background: #444;
    color: #ddd;
    text-align: center;
}

.calc {
    margin: 30px auto;
    padding: 20px 0 0;
    border: 2px dashed #444;
    border-radius: 20px;
    width: 50%;
}

.calcResult {
    margin-top: 20px;
}

.calcInput {
    line-height: 30px;
    width: 90%;
    margin: 5px auto;
    border-radius: 20px;
    text-align: center;
    font-size: 2.5rem;
    font: Verdana, Geneva, Tahoma, sans-serif;
    text-shadow: 1px 1px#444;
}

.calcBtns {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.calcBtns button {
    margin: 5px;
    background-color: yellowgreen;
    font-size: 2.5rem;
    border: none;
    flex: 1;
}

.calcResult {
    width: 100%;
    background-color: #444;
    border-radius: 0 0 20px 20px;
    border: 2px dashed #444;
    min-height: 60px;
    color: #ddd;
    font-size: 2.5rem;
    text-align: center;
    text-shadow: 1px 1px #ddd;
}